.loginPageContainer {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(120deg, #3498db, #c064e7);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    overflow: hidden;

    .loginPageContentWrapper {
        width: 400px;
        height: 80%;
        max-height: 600px;
        background-color: #eee;
        border-radius: 10px;
        box-shadow: 2px 2px 5px #ccc;
        overflow: hidden;

        .loginPageImgWrapper {
            width: 100%;
            height: 40%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;

            &:before {
                content: "";
                position: absolute;
                border-left: 400px solid #eee;
                border-top: 30px solid transparent;
                bottom: 0;
                z-index: 1;
            }

            img {
                width: 100%;
                height: 100%;
                border-radius: 10px 10px 0px 0px;
                opacity: 0.8;
            }
        }

        .loginPageAccountWrapper {
            width: 100%;
            height: 45%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            .loginPageLoginBtn {
                margin-top: 10px;
                width: 40%;
                height: 40px;
                border: none;
                border-radius: 30px;
                background: linear-gradient(120deg, #c064e7, #3498db, #f441a5, #4e91e9);
                background-size: 400%;
                text-align: center;
                line-height: 40px;
                color: #fff;
                font-size: 20px;
                box-sizing: border-box;
                transition: 4s;
                position: relative;

                &:hover {
                    background-position: right;
                }
            }

            .loginPageAccountWrapperInputWrapper {
                width: 80%;
                margin-bottom: 30px;
                position: relative;

                input {
                    width: 100%;
                    background-color: transparent;
                    border: none;
                    border-bottom: 2px solid rgba(180, 180, 180, 0.7);
                    font-size: 15px;
                    color: 333;
                    outline: none;
                    position: relative;
                    padding: 5px;
                    transition: 0.5s;
                    z-index: 2;
                    cursor: pointer;

                    &:focus,
                    &:valid {
                        border-bottom: 2px solid #0264f6;

                        +span {
                            color: #0264f6;
                            font-size: 13px;
                            transform: translateY(-20px);
                        }
                    }
                }

                span {
                    color: #aaa;
                    font-size: 18px;
                    position: absolute;
                    left: 5px;
                    top: 0px;
                    transition: 0.5s;
                    z-index: 1;
                }
            }
        }

        .loginPageFooterWrapper {
            display: flex;
            align-items: flex-end;
            width: 100%;
            height: 15%;
            background: linear-gradient(120deg, #c064e7, #4e91e9);
            box-sizing: border-box;
            font-size: 20px;
            font-weight: bold;
            font-family: 'Lobster', cursive;
            color: #fff;
            position: relative;
            padding: 20px;

            &:after {
                content: "";
                position: absolute;
                border-right: 400px solid #eee;
                border-bottom: 30px solid transparent;
                left: 0;
                top: 0;
            }
        }
    }
}